import React, { Component } from 'react';
import PropTypes from 'prop-types';

/*
* Компонент Header.
*
* Классовый компонент, рендерит header вида nav>ul>li*N от кол-ва объектов в stateData.
* Все данные для построения UI берутся из datalist.js (см строка 39 let dataArray = datalist();)
* Аргументы:
* @input stateData (используются active и id)
*/

export default class Header extends Component {
  render() {
    return (
      <header className="header">
        <div className="header__container">
          <div className="header__logo">RecAs</div>
          <div className="header__navigate">
            <nav className="navigate">
              <ul>
                {
                  this.props.stateData.map(itemData => {
                    return <li key={itemData.id} className={itemData.active} onClick={() => this.props.onClick(itemData.id)}>
                            <img key={`img${itemData.id}`} src={itemData.src} alt={itemData.alt} title={itemData.title} className='find__recipes' />
                           </li>

                  })
                }
              </ul>
            </nav>
          </div>
        </div>
      </header>
    )
  }
};


Header.propTypes = {
  stateData: PropTypes.arrayOf(PropTypes.object).isRequired
};


