let sliderMethods = {
  nextcard() {
    this.state.indexPosition === this.props.catalog.length-1 ? 
    this.setState({indexPosition: 0}) : 
    this.setState({indexPosition: this.state.indexPosition+1});
    this.setState({cardId: this.state.cardId.toString().slice(-1)+this.state.indexPosition});
  },
  previoscard() {
    this.state.indexPosition === 0 ? 
    this.setState({indexPosition: this.props.catalog.length-1}) : 
    this.setState({indexPosition: this.state.indexPosition-1});
    this.setState({cardId: this.state.cardId.toString().slice(-1)+this.state.indexPosition});
  },
  touchstart(e) {
    this.setState({
      touchStartX: e.targetTouches[0].pageX,
      touchStartY: e.targetTouches[0].pageY,
    })
  },
  touchend(e) {
    let endtouchX = e.changedTouches[0].pageX;
    let endtouchY = e.changedTouches[0].pageY;
    if ((this.state.touchStartX > endtouchX && this.state.touchStartX-endtouchX > 75) && 
       ((this.state.touchStartY-endtouchY<100 && this.state.touchStartY-endtouchY>-100) || 
       (endtouchY-this.state.touchStartY>-100 && endtouchY-this.state.touchStartY<-100))) {
         this.nextcard();
    } else if (((this.state.touchStartX < endtouchX && endtouchX-this.state.touchStartX > 75) || (endtouchX-this.state.touchStartX < -75)) && 
              ((this.state.touchStartY-endtouchY<100 && this.state.touchStartY-endtouchY>-100) || 
              (endtouchY-this.state.touchStartY>-100 && endtouchY-this.state.touchStartY<-100))) {
                this.previoscard();
    };
  }
}

export default sliderMethods;


