let mixin = {
  //Метод для получения продуктов со всех карточек и поиска совпадений в полном перечне рецептов
  getRecipes() {
    //Получаем список всех ингридиентов/продуктов для поиска в полном перечне рецептов
    let dataForms = [];
    let recipes = this.props.recipes;

    Array.from(document.forms).forEach( form => {         
      Array.from(form.elements).forEach( itemForm => dataForms.push({[itemForm.name] : Number(itemForm.value)}))
    })
    //console.log(dataForms)
    //console.log(recipes)

    //Поиск рецептов по имеющейся коллекции ингридиентов
    //Должен вернуть параментр true, если рецепты найдены и коллекцию рецептов для рендера
    let findvalue = new Set();

    dataForms.forEach(item => {
      for(let i = 0; i<recipes.length; i++) {
        //Включить для дебага
        //console.log(Object.keys(recipes[i].forfiltering))
        //console.log(Object.keys(item)[0])
        //console.log(Object.keys(recipes[i].forfiltering).includes(Object.keys(item)[0].toLowerCase().toString()))
        //console.log(Object.values(item)[0])
        if(Object.keys(recipes[i].forfiltering).includes(Object.keys(item)[0].toLowerCase().toString()) && Object.values(item)[0] > 0) findvalue.add(recipes[i]);
      }
    })
    //console.log(Array.from(findvalue));
    findvalue.size > 0 ? this.setState({findRecipe: 1, recipes: Array.from(findvalue)}) : this.setState({findRecipe: 2})
  }
};

export default mixin;


