import React, { Component } from 'react';
import PropTypes from 'prop-types';

/*
* Компонент создает карточки form для ввода продуктов
*
* Конечный компонент, не имеет дочерних компонент
* getInputs можно заменить на фрагменты, но использем div для визуализации и простоты работы со стилями
* На вход принимает:
* @input dataitems - данные для создания карточки
* @input state - объект с данными из this.state родительского компонента
*/

export default class MainBlockCards extends Component {
  getCards(items, state) {
    let cardlist = items.map( (value, index ) =>{
      let listClassNames = 'formfood';
      if(state.cardId === value.id || (state.indexPosition === 0 && index === 0) || state.indexPosition === index) {
        listClassNames += ' fulllist__active';
      }
      return (
        <li key={value.id}>
          <form className={listClassNames} id={value.id} name={`formfood-${value.id}`}>
          <legend>{`${value.name} (${value.measure})`}</legend>
            {this.getInputs(value.labelname, value.id)}
          </form>
        </li>
      )
    });
    return cardlist;
  };
  getInputs(items, id) {
    let listinputs = items.map((value,index)=>{
      return (
        <div className='formfood__block' key={index}>
          <label htmlFor={`${id}food-${index}`}>{`${value}:`}</label>
          <input type="number" id={`${id}food-${index}`} placeholder='0' className='inputCard' name={value} />
        </div>
      )
    });
    return listinputs;
  };

  render() {
    if(this.props.dataitems.length > 0) {
      return (
        <div className='selection_cards'>
          <ul className='fulllist'>
            {this.getCards(this.props.dataitems, this.props.state)}
          </ul>  
          <button className="selection__findButton main__btn" type="submit" onClick={() => this.props.getRecipes()}>Поиск</button>
        </div>
      )
    };
    return (
      <div className="selection_cards">Загрузка...</div>
    )
  };
};


MainBlockCards.propTypes = {
  dataitems: PropTypes.arrayOf(PropTypes.object).isRequired,
  state: PropTypes.object.isRequired,
};


