import React, { Component } from 'react';
import get_DataJson from '../ModuleJS/get_DataJson';
import datalist from '../ModuleJS/datalist';
import MainBlockRecipeRender from './MainBlock__components/MainBlock-recipe-render';
import MainBlockSearchRecipes from './MainBlock__components/MainBlock-search-recipes';
import ErrorBoundary from './ErrorBoundary';
import PropTypes from 'prop-types';

/*
* Компонент управляющий 2 вкладками: Поиск рецепта и Полный перечень.
*
* Догружает в себя полный список рецептов и каталог для создания кнопок и карточек.
* Содержит в себе 2 дочерних компонента (поиск MainBlockSearchRecipes и полный перечень MainBlockRecipeRender), на вход не принимает пропсы, только отдает их
* дочерним элементам из state.
* Все данные для построения UI берутся из datalist.js (см строка 37 let dataArray = datalist();)
*/

export default class MainBlock extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: 'is loading...', //'is loading...' отрендерится пока мы ждем ответ по API
      catalog: [],
      recipes: [],
      stateFlag: this.props.stateFlag,
    };
  };
  componentDidMount() {
    //Получаем по API рандомную шутку для подзаголовка
    get_DataJson('https://official-joke-api.appspot.com/random_joke')
      .then((result) => {
        this.setState({
          data: result.setup + " " + result.punchline
        })
      });
    //получаем из объектов массив кнопок и списков, рецепты
    let dataArray = datalist();
    this.setState({
      catalog: dataArray[0], //получаем каталог кнопок и карточек
      recipes: dataArray[1], //получаем коллекцию рецептов
    });
  };

  render() {
    if(this.props.stateFlag) {
      return (
        //рендерим вкладку Поиск рецепта
        <ErrorBoundary>
           <MainBlockSearchRecipes catalog={this.state.catalog} recipes={this.state.recipes} data={this.state.data} />
        </ErrorBoundary>
      ) 
    };
    return (
      //рендерим вкладку Полный перечень
      <ErrorBoundary>
        <MainBlockRecipeRender recipes={this.state.recipes} optionParametr={0}/>
      </ErrorBoundary>
    );
  };
};


MainBlock.propTypes = {
  data: PropTypes.string,
  catalog: PropTypes.arrayOf(PropTypes.object),
  recipes: PropTypes.arrayOf(PropTypes.object),
  stateFlag: PropTypes.bool,
};


