import './main.css';
import Header from './ReactComponents/Header.js';
import MainBlock from './ReactComponents/MainBlock.js';
import React from 'react';
import PropTypes from 'prop-types';
import Context from './ReactComponents/Context';


export default function App() {
  const [stateData, setState] = React.useState([
    {
      statement: true,
      active: 'navigate__link active',
      id: 'findRecepts',
      src: 'img/free-icon-recipebook.png',
      alt: 'Поиск рецепта',
      title: 'Поиск рецепта',
    },
    {
      statement: false,
      active: 'navigate__link',
      id: 'fullRecepts',
      src: 'img/free-icon-recipe.png',
      alt: 'Полный перечень',
      title: 'Полный перечень',
    },
  ]);
  function isChange(id) {
    setState(
      stateData.map(item => {
      if(item.id === id && !item.statement) {
        item.statement = !item.statement;
        item.active = 'navigate__link active'
      } else if(item.id === id && item.statement) {
        item.active = 'navigate__link active'
      } else {
        item.statement = false;
        item.active = 'navigate__link'
      }
      
      return item
      })
    )
  };
  return (
    //по клику в Header возвращаем id для изменения состояния
    //после смены состояния происходит перерендер (рендерится нужная нам вкладка в MainBlock)
    <div className="App">
      <Context.Provider value={'Перечень рецептов'}>
        <Header stateData={stateData} onClick={isChange}/>
        <MainBlock stateFlag={stateData[0].statement}/>
      </Context.Provider>
    </div>
  );
};


App.propTypes = {
  stateData: PropTypes.arrayOf(PropTypes.object),
  statement: PropTypes.bool,
  active: PropTypes.string,
  id: PropTypes.string,
};


