import React from 'react';
import { useContext } from 'react';
import Context from '../../Context';
import PropTypes from 'prop-types';

/*
* Компонент для добавления header на вкладке Полный перечень
* Данные берутся из контекста и из 2 входящих аргументов
* @input optionParametr - тип выводимой информации (0/1/2)
* @input count - кол-во найденных рецептов
*
* Всего 3 вида отражаемых данных - стандартный, найдено N рецептов и не найдено ни одного
*/

export default function MainBlockRecipeHeader( {optionParametr, count} ) {
  const sklonenie = (number, txt, cases = [2, 0, 1, 1, 1, 2]) => txt[(number % 100 > 4 && number % 100 < 20) ? 2 : cases[(number % 10 < 5) ? number % 10 : 5]];
  let recipesAbr = sklonenie(count, ['рецепт', 'рецепта', 'рецептов']);
  
  const data = useContext(Context);
  if(optionParametr===2) {
    return (
      <div className='selection__preview norecipe'>
        <h1>{data}</h1>
        <h3 className='nodata'>К сожалению по указанным Вами ингридиентам не найдено ни одного рецепта</h3>
        <button type="submit" className='selection__backbutton main__btn' onClick={()=> document.location.reload()}>Повторить поиск</button>
      </div>
    )
  } else if(optionParametr===0) {
    return (
      <div className='selection__preview'>
        <h1>{data}</h1>
        <h3 className='datafound'>{`Найдено: ${count} ${recipesAbr}`}</h3>
      </div>
    )
  }
  return (
    <div className='selection__preview'>
      <h1>{data}</h1>
      <h3 className='datafound'>{`Найдено: ${count} ${recipesAbr}`}</h3>
      <button type="submit" className='selection__backbutton main__btn' onClick={()=> document.location.reload()}>Повторить поиск</button>
    </div>
  )
};


MainBlockRecipeHeader.propTypes = {
  data: PropTypes.string,
  count: PropTypes.number,
  recipesAbr: PropTypes.string,
}


