export default function get_JSON(url) {
  async function getData(url) {
    try {
      const response = await fetch(url, {
        method: 'GET',
      });
      let result = await response.json();
      return await result;
    } catch(err) {
      return {
              setup: `Error! `,
              punchline: `${err}`
            }; // TypeError: failed to fetch
    }
  }
  
  return getData(url);
}


