import React from 'react';
import PropTypes from 'prop-types';

/*
* Функциональный компонент, который возвращает button
*
* Не имеет дочерних компонент, возвращает данные для родительского компонента, работая в map
* @inpit data - массив с именем, индексом, id, позицией активной кнопки, а так же функция-колбэк
*/

export default function MainBlockMenuButton( {data} ) {
  let [value, index, id, indexPosition, onClick] = data;
  if(index===indexPosition) return <button className="main__btn activebtn" name={value} aria-label={value} onClick={() => onClick(id, index)}>{value}</button>
                            return <button className="main__btn" name={value} aria-label={value} onClick={() => onClick(id, index)}>          {value}</button>
};

MainBlockMenuButton.propTypes = {
  data: PropTypes.array.isRequired,
  value: PropTypes.string,
  index: PropTypes.number,
};


