import React, { Component } from 'react';
import MainBlockCards from './MainBlock-Menu-Components/MainBlock-cards';
import MainBlockMenu from './MainBlock-Menu-Components/MainBlock-menu';
import mixin from '../../ModuleJS/get_recipe';
import sliderMethods from '../../ModuleJS/slider';
import MainBlockRecipeRender from './MainBlock-recipe-render';
import PropTypes from 'prop-types';

/*
* Компонент управляющий 2 состояниями - рендер меню и карточек для ввода или рендер найденных рецептов
*
* Компонент использует примесь из get_recipe.js (строка 72)
* Взависимости от состояния, т.е. от значения переменной findRecipe зависит рендер контента:
* 1. true - рендер найденных рецептов
* 2. false - рендер стандартного меню (список кнопок и карточек для ввода) *используется по-умолчанию
*
* На вход получает 3 переменные - список рецептов, список конопок и полную карту названий, рандомную шутку для подзаголовка :)
*/


export default class MainBlockSearchRecipes extends Component {
  constructor(props) {
    super(props);
    this.state = {
      cardId: '',
      indexPosition: 0,
      findRecipe: 0,
      recipes: [],
      touchStartX: 0,
      touchStartY: 0,
    };
    this.getRecipes = this.getRecipes.bind(this);
    
    this.nextcard = this.nextcard.bind(this);
    this.previoscard = this.previoscard.bind(this);
    this.touchstart = this.touchstart.bind(this);
    this.touchend = this.touchend.bind(this);
  };

  render() {
    //рендер найденных рецептов (если state findRecipe===1)
    if(this.state.findRecipe) {
      return <MainBlockRecipeRender recipes={this.state.recipes} optionParametr={this.state.findRecipe}/>
    }
    return (
      //рендер меню и контента по-умолчанию, до получения результатов поиска
      <main className="selection">
        <div className="selection__preview">
          <h1>Recipe assistant</h1>
          <h5>{'Random jokes: '+ this.props.data}</h5>
        </div>
        <div className="selection__container">
          <section className="selection__menu">
            <MainBlockMenu dataitems={this.props.catalog} indexPosition={this.state.indexPosition} isChange={ (id, index) => {
              this.setState({
                cardId: id,
                indexPosition: index,
              })
            }}/>
          </section>
          <section className="selection__list">
            <div className="selection__leaf backward main__btn" onClick={()=> this.previoscard()}>{'<'}</div>
              <div 
                onTouchStart={(e)=> {
                  this.touchstart(e);
                }}
                onTouchEnd={(e)=> {
                  this.touchend(e);
                }}>

              <MainBlockCards dataitems={this.props.catalog} state={this.state} getRecipes={this.getRecipes} />

            </div>

            <div className="selection__leaf next main__btn" onClick={()=> this.nextcard()}>{'>'}</div>
          </section>  
        </div>
      </main>
    )
  };
};

Object.assign(MainBlockSearchRecipes.prototype, mixin);
Object.assign(MainBlockSearchRecipes.prototype, sliderMethods);

MainBlockSearchRecipes.propTypes = {
  cardId: PropTypes.string,
  indexPosition: PropTypes.number,
  findRecipe: PropTypes.bool,
  recipes: PropTypes.arrayOf(PropTypes.object).isRequired,
};


