import React, { Component } from 'react';
import MainBlockMenuButton from './MainBlock-menu-buttons';
import PropTypes from 'prop-types';

/*
* Компонент рендерит кнопки для управления карточками продуктов
*
* Имеет 1 дочерний компонент, из которого в цикле получает кнопки, перебирая коллекцию данных dataitems
* Пока данные из асинхронного запроса не получены будет отрендерена строка <div className="selection__btn">Загрузка...</div>
* На вход принимает 2 аргумента:
* @inpit dataitems - каталог, список кнопок и полную карту наименований
* @inpit indexPosition - позиция активной кнопки
*/


export default class MainBlockMenu extends Component {
  render() {
    if(this.props.dataitems.length > 0) {
      return (
        <div className="selection__btn">
          {

            this.props.dataitems.map((value,index)=>{
              return <MainBlockMenuButton data={
                [value.name, 
                 index, 
                 value.id, 
                 this.props.indexPosition,
                 (id, index)=> {this.props.isChange(id, index)}
                ]} 
                key={value.key}/>
            })

          }
        </div>
      )
    };
    return (
      <div className="selection__btn">Загрузка...</div>
    )
  };
};


MainBlockMenu.propTypes = {
  dataitems: PropTypes.arrayOf(PropTypes.object).isRequired
};


