export default function datalist() {
const catalog = [{
                labelname: ['Гречка','Рис','Перловка','Кускус','Булгур', 'Макароны'],
                measure: 'грамм',
                name: 'Крупы и макароны',
                key: 'v1-k1',
                id: 'bt1k1',
              },
              {
                labelname: ['Свинина','Баранина','Говядина', 'Курица'],
                measure: 'грамм',
                name: 'Мясо',
                key: 'v1-k2',
                id: 'bt1k2',
              },
              {
                labelname: ['Сосиски','Сардельки','Копченая колбаса','Полукопченая колбаса', 'Ливерная колбаса'],
                measure: 'грамм',
                name: 'Колбасы',
                key: 'v1-k3',
                id: 'bt1k3',
              },
              {
                labelname: ['Лук','Лук-порей','Морковь','Помидор','Огурец','Картофель'],
                measure: 'шт',
                name: 'Овощи',
                key: 'v1-k4',
                id: 'bt1k4',
              },
              {
                labelname: ['Сливочное','Оливковое','Подсолнечное'],
                measure: 'гр',
                name: 'Масло',
                key: 'v1-k5',
                id: 'bt1k5',
              },
              {
                labelname: ['Сливки', 'Оливки', 'Маслины', 'Сметана'],
                measure: 'гр',
                name: 'Прочее',
                key: 'v1-k6',
                id: 'bt1k6',
              },
              ]
const recipes = [{
                labelname: 'Солянка с колбасой',
                img: 'img/001.jpg',
                forfiltering: {
                  картофель: 6,
                  курица: 300,
                  лук: 1,
                },
                ingridient: [
                  'картофель – 6-7 шт.',
                  'питьевая вода – 2,7 л',
                  'вареная колбаса (лучше «докторская») или сосиски – 300 г',
                  'грудинка курицы – 300 г',
                  'варено-копченая колбаса – 250 г',
                  'растительное масло – 2 ст. л.',
                  'соленые огурцы – 6-7 шт.',
                  'репчатый лук - 1 шт.',
                  'томатная паста – 2 ст. л.',
                  'огуречный рассол – 200 мл',
                  'консервированные маслины с косточкой – 150 г',
                  'лимон для подачи – по вкусу',
                  'соль, молотый черный перец – по вкусу',
                ],
                information: "Cолянка с колбасой — блюдо для тех, кто любит вкусно поесть, но не готов тратить на приготовление пищи слишком много времени."
              },{
                labelname: 'Борщ классический',
                img: 'img/002.jpg',
                forfiltering: {
                  картофель: 2,
                  говядина: 500,
                  лук: 3,
                  свекла: 2,
                  морковь: 2,
                  чеснок: 1,
                },
                ingridient: [
                  '500 г говяжьей грудинки на кости',
                  '3 средние луковицы',
                  '2 небольшие свеклы',
                  '2 средние картофелины',
                  '2 небольшие морковки',
                  'половина небольшого кочана капусты',
                  '4–5 зубчиков чеснока',
                  '1 пучок петрушки',
                  '2 ст. л. томатной пасты',
                  '1 ст. л. яблочного уксуса',
                  '1 лавровый лист',
                  'щепотка сахара',
                  'соль, свежемолотый черный перец',
                  'сметана для подачи',
                ],
                information: "Особое удовольствие в холодный период года — классический борщ с говядиной. Простой в приготовлении и вкусный суп!"
              },{
                labelname: 'Макароны с тушенкой',
                img: 'img/003.jpg',
                forfiltering: {
                  макароны: 400,
                  тушенка: 400,
                },
                ingridient: [
                  'Макароны - 400гр',
                  'питьевая вода – 2,7 л',
                  '2 банки тушеной говядины (высший сорт)',
                  'приправы и зелень по вкусу',
                ],
                information: "Макароны с тушенкой — блюдо простое и быстро приготавливаемое. Макароны — это универсальный продукт, который можно  успешно сочетать с чем угодно!"
              },{
                labelname: 'Макароны с сосисками',
                img: 'img/004.jpg',
                forfiltering: {
                  макароны: 400,
                  сосиски: 200,
                  сыр: 200,
                },
                ingridient: [
                  'Макароны - 400гр',
                  'питьевая вода – 2,7 л',
                  'упаковка сосисок (400гр)',
                  'сыр твердый 200гр',
                  'приправы и зелень по вкусу',
                ],
                information: "Макароны с сосками — по-праву одно из самых быстрых в пригтовлении блюд. Макароны — это универсальный продукт, который можно  успешно сочетать с чем угодно!"
              },{
                labelname: 'Отварная говядина с рисовым гарниром',
                img: 'img/005.jpg',
                forfiltering: {
                  говядина: 800,
                  рис: 400,
                  'лук-порей': 0.5,
                  огурец: 2,
                },
                ingridient: [
                  '800 г бескостной говядины',
                  '2 веточки укропа',
                  '2 веточки петрушки',
                  'веточка тимьяна',
                  '1 лавровый лист',
                  '5 горошин черного перца',
                  '2 стакана круглозерного риса',
                  '2 небольших свежих огурчика',
                  '0,5 стебля лука-порея',
                  'соевый соус',
                ],
                information: "Отварная говядина с рисовым гарниром - легко приготавливаемое, вкусное блюдо. Мясо и рис готовятся отдельно, что может ускорить процесс приготовления блюда."
              },
              ]
  return [catalog,recipes]
}


